import CryptoJS from 'crypto-js';
import Worker from 'worker-loader?inline=true&fallback=false!./fixtransformpcm.worker'

// 音频转码worker
let recorderWorker = new Worker;
// 记录处理的缓存音频
let buffer = [];
let AudioContext = window.AudioContext || window.webkitAudioContext;
let notSupportTip = '目前仅支持chrome浏览器以及https协议访问语音功能';
navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;

recorderWorker.onmessage = function(e) {
    buffer.push(...e.data.buffer)
};

class IatRecorder {
    constructor(config) {
        this.config = config;
        this.state = 'ing';
        this.language = config.language || 'zh_cn';
        this.accent = config.accent || 'mandarin';

        this.appId = '5dfc8239';
        this.apiKey = '103f72d7cd32e8cb934f137f301e3651';
        this.apiSecret = 'af46255b27235b71976c4a63440c28e5'
    }

    start() {
        // this.stop();
        if (navigator.getUserMedia && AudioContext) {
            this.state = 'ing';
            if (!this.recorder) {
                let context = new AudioContext();
                this.context = context;
                this.recorder = context.createScriptProcessor(0, 1, 1);

                let getMediaSuccess = (stream) => {
                    this.mediaStream = this.context.createMediaStreamSource(stream);
                    this.recorder.onaudioprocess = (e) => {
                        this.sendData(e.inputBuffer.getChannelData(0))
                    };
                    // important
                    this.connectWebsocket()
                };
                let getMediaFail = (e) => {
                    this.recorder = null;
                    this.mediaStream = null;
                    this.context = null;
                    console.log('请求麦克风失败')
                };

                if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
                    navigator.mediaDevices.getUserMedia({
                        audio: true,
                        video: false
                    }).then((stream) => {
                        getMediaSuccess(stream)
                    }).catch((e) => {
                        getMediaFail(e)
                    })
                } else {
                    navigator.getUserMedia({
                        audio: true,
                        video: false
                    }, stream => {
                        getMediaSuccess(stream)
                    }, e => {
                        getMediaFail(e)
                    })
                }
            } else {
                this.connectWebsocket()
            }
        } else {
            let isChrome = navigator.userAgent.toLowerCase().match(/chrome/);
            alert(notSupportTip);
            console.log('must use chrome')
        }
    }

    stop() {
        this.state = 'end';
        try {
            this.mediaStream.disconnect(this.recorder);
            this.recorder.disconnect()
        } catch (e) {}
    }

    sendData(buffer) {
        recorderWorker.postMessage({
            command: 'transform',
            buffer: buffer
        })
    }

    connectWebsocket() {
        let url = 'wss://iat-api.xfyun.cn/v2/iat';
        let host = 'iat-api.xfyun.cn';
        let apiKey = this.apiKey;
        let apiSecret = this.apiSecret;
        let date = new Date().toGMTString();
        let algorithm = 'hmac-sha256';
        let headers = 'host date request-line';
        let signatureOrigin = `host: ${host}\ndate: ${date}\nGET /v2/iat HTTP/1.1`;
        let signatureSha = CryptoJS.HmacSHA256(signatureOrigin, apiSecret);
        let signature = CryptoJS.enc.Base64.stringify(signatureSha);
        let authorizationOrigin = `api_key="${apiKey}", algorithm="${algorithm}", headers="${headers}", signature="${signature}"`;
        let authorization = btoa(authorizationOrigin);
        url = `${url}?authorization=${authorization}&date=${date}&host=${host}`;
        if ('WebSocket' in window) {
            this.ws = new WebSocket(url)
        } else if ('MozWebSocket' in window) {
            this.ws = new MozWebSocket(url)
        } else {
            alert(notSupportTip);
            return null
        }
        this.ws.onopen = (e) => {
            this.mediaStream.connect(this.recorder);
            this.recorder.connect(this.context.destination);
            setTimeout(() => {
                this.wsOpened(e)
            }, 500);
            this.config.onStart && this.config.onStart(e)
        };
        this.ws.onmessage = (e) => {
            this.config.onMessage && this.config.onMessage(e);
            this.wsOnMessage(e)
        };
        this.ws.onerror = (e) => {
            this.stop();
            this.config.onError && this.config.onError(e)
        };
        this.ws.onclose = (e) => {
            this.stop();
            this.config.onClose && this.config.onClose(e)
        }
    }

    wsOpened() {
        if (this.ws.readyState !== 1) {
            return
        }
        let audioData = buffer.splice(0, 1280);
        // console.log('audioData', audioData.length);
        let params = {
            'common': {
                'app_id': this.appId
            },
            'business': {
                'language': this.language, //小语种可在控制台--语音听写（流式）--方言/语种处添加试用
                'domain': 'iat',
                'accent': this.accent, //中文方言可在控制台--语音听写（流式）--方言/语种处添加试用
                'vad_eos': 5000,
                'dwa': 'wpgs' //为使该功能生效，需到控制台开通动态修正功能（该功能免费）
            },
            'data': {
                'status': 0,
                'format': 'audio/L16;rate=16000',
                'encoding': 'raw',
                'audio': this.ArrayBufferToBase64(audioData)
            }
        };
        this.ws.send(JSON.stringify(params));
        this.handlerInterval = setInterval(() => {
            // websocket未连接
            if (this.ws.readyState !== 1) {
                clearInterval(this.handlerInterval);
                return
            }
            if (buffer.length === 0) {
                if (this.state === 'end') {
                    this.ws.send(JSON.stringify({
                        'data': {
                            'status': 2,
                            'format': 'audio/L16;rate=16000',
                            'encoding': 'raw',
                            'audio': ''
                        }
                    }));
                    clearInterval(this.handlerInterval)
                }
                return false
            }
            audioData = buffer.splice(0, 1280);
            // 中间帧
            this.ws.send(JSON.stringify({
                'data': {
                    'status': 1,
                    'format': 'audio/L16;rate=16000',
                    'encoding': 'raw',
                    'audio': this.ArrayBufferToBase64(audioData)
                }
            }))
        }, 40)
    }

    wsOnMessage(e) {
        let jsonData = JSON.parse(e.data);
        // 识别结束
        if (jsonData.code === 0 && jsonData.data.status === 2) {
            this.ws.close()
        }
        if (jsonData.code !== 0) {
            this.ws.close();
            console.log(`${jsonData.code}:${jsonData.message}`)
        }
    }

    ArrayBufferToBase64(buffer) {
        let binary = '';
        let bytes = new Uint8Array(buffer);
        let len = bytes.byteLength;
        for (let i = 0; i < len; i++) {
            binary += String.fromCharCode(bytes[i])
        }
        return window.btoa(binary)
    }
}

export class IatTaste {
    constructor(callback) {
        this.iatRecorder = new IatRecorder({
            onClose: () => {
                this.stop();
                this.reset()
            },
            onError: (data) => {
                this.stop();
                this.reset();
                alert('WebSocket连接失败')
            },
            onMessage: (e) => {
                let jsonData = JSON.parse(e.data);
                if (jsonData.data && jsonData.data.result) {
                    this.setResult(jsonData.data.result)
                }
            },
            onStart: () => {}
        });
        this.resultText = '';
        this.callback = callback
    }

    setResult(data) {
        let str = '';
        let ws = data.ws;
        for (let i = 0; i < ws.length; i++) {
            str = str + ws[i].cw[0].w
        }
        // 开启wpgs会有此字段(前提：在控制台开通动态修正功能)
        // 取值为 "apd"时表示该片结果是追加到前面的最终结果；取值为"rpl" 时表示替换前面的部分结果，替换范围为rg字段
        // if (data.pgs !== 'apd') {
        //   this.resultText = ''
        // }
        this.resultText = this.resultText + str;
        console.log(this.resultText)
        this.callback(this.resultText, false);
        return this.resultText;
    }

    init() {
        // none

    }

    start() {
        this.iatRecorder.start()
    }

    stop() {
        this.iatRecorder.stop();
        this.callback('stop', true)
    }

    reset() {
        buffer = []
    }

    active() {
        if (navigator.getUserMedia && AudioContext && recorderWorker) {
            this.resultText = '';
            this.start()
        } else {
            this.callback('notSupport http', true);
            alert(notSupportTip)
        }
    }

    inactive() {
        this.reset();
        this.stop()
    }
}


