<template>
  <div class="audio-box" @click="isListening = !isListening">
    <svg class="icon" aria-hidden="true"
         :class="isListening? 'listening': ''">
      <use xlink:href="#icon-audio"></use>
    </svg>
    <svg class="icon pulse" aria-hidden="true">
      <use xlink:href="#icon-audio"></use>
    </svg>
  </div>
</template>

<script>
  import { IatTaste } from "@/common/utils/xunfei/iat.js";

  export default {
    name: "AudioInput",
    data() {
      return {
        iatTaste: undefined,
        isListening: false
      }
    },
    watch: {
      isListening(newVal) {
        if (newVal) {
          this.iatTaste.active()
        } else {
          this.iatTaste.inactive()
        }
      }
    },
    mounted() {
      this.iatTaste = new IatTaste((text, stopFlag) => {
        console.log(text);
        if (stopFlag) { 
			console.log(stopFlag);
          this.isListening = false
        } else {
          this.$emit('audio-text', text);
        }
        
      });
      this.iatTaste.init()
    }
  }
</script>

<style scoped>
  .audio-box {
    position: absolute;
	margin-left: 570px;
	margin-top: -35px;
    width: 31px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .listening {
    color: #FFB301;
  }

  .pulse { 
	  width: 31px;
	  height: 32px;
    position: absolute;
  }

  .listening+.pulse {
    color: var(--color-text-attention);
    /*display: none;*/
    -webkit-animation: pulse 1s infinite ease-in;
    /* Chrome, Safari, Opera */
    animation: pulse 1s infinite ease-in;
  }

  .icon {
    font-size: 1.5rem;
  }
  @keyframes pulse {
    0% {
      transform: scale(1);
      opacity: 0.5;
    }
    100% {
      transform: scale(2) translateY(-4px);
      opacity: 0;
    }
  }
  
  @-webkit-keyframes pulse {
    0% {
      -webkit-transform: scale(1);
      opacity: 0.5
    }
    100% {
      -webkit-transform: scale(2) translateY(-4px);
      opacity: 0
    }
  }
  
 
  .animation-pulse {
    background: inherit;
    width: 100%;
    height: 100%;
    -webkit-animation: pulse 1s infinite ease-in;
    /* Chrome, Safari, Opera */
    animation: pulse 1s infinite ease-in;
  }
</style>



